.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-color: #8a2b06;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 10;
}
.image {
    width: 100%;
    height: 25rem;
    z-index: 0;
    overflow: hidden;
}
.image img{
    width: 110%;
    height: 100%;
    object-fit: cover;
    transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}
@media (max-width: 590px) {
    .header h1{
        font-size: 1.5rem;
    }
}
@media (max-width: 480px) {
    .header h1{
        font-size: 1.3rem;
    }
  }